
.navBar {
    width: 230px;
    height: 100px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navBar__link {
    background-color: white;
    color: blue;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 40px;
    text-decoration: none;
    font-weight: 600;
    font-size: 15px;
}