* {
    box-sizing: border-box;
}

html {
    background-color: rgba(143, 177, 189, 0.3);
}

.loader_text {
    position: absolute;
    text-align: center;
    font-size: 25px;
    width: 300px;
    height: 50px;
    left: calc(50vw - 150px);
    top: calc(50vh - 25px);
}
    
.movies {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 50px;
}

.movies .movies__movie {
    width: 620px;
    height: 320px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    background-color: white;
    padding: 30px;
    margin-bottom: 30px;
    box-shadow: 0px 3px 8px 6px rgba(0,0,0,.2);
}
.movie__poster {
    max-width: 170px;
    object-fit: contain;
    margin-right: 20px;
    transform: translateY(-40px);
    box-shadow: 0px 10px 8px 3px rgba(0,0,0,.2);
}
.movie__title {
    font-size: 22px;
    font-weight: 600;
    margin: 15px 0 10px 0;
}
.movie__year {
    opacity: 0.5;
    margin: 0 0 5px 0;
}
.movie__genres {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
}
.genres__genre {
    list-style: none;
    margin-right: 5px;
    font-size: 15px;
    opacity: 0.5;
}
.movie__summary {
    font-size: 16px;
    opacity: 0.5;
    line-height: 1.5;
    height: 145px;
    overflow: hidden;
}